  margin: 0 auto;
}

.container-xl {
  max-width: 1320px;
  width: 100%;
  margin: 0 auto;
}

.container-xxl {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
