  .header .burger-container .burger-menu {
    display: block;
    position: relative;
    height: 32px;
    width: 42px;
  }
  .header .burger-container .burger-menu:before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: black;
  }
  .header .burger-container .burger-menu:after {
    content: "";
    position: absolute;
    display: block;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: black;
  }
  .header .burger-container .burger-menu .burger-inner {
    content: "";
    position: absolute;
    display: block;
    top: 50%;
    left: 0;
    height: 2px;
    width: 100%;
    background: black;
  }
}
